

import store from '@/store/index';

import { mapActions } from 'vuex';

import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

export default {

name: 'HelloWorld',

data () {
return {
valueSearch: '',

list2: ['Search in Appleton Tower', 'Search in Campus'],

index2: 0,
demo2: 'Search in Appleton Tower',

bottomCount: [
'Classroom (entre the name of the classroom)',
'Printer',
'Computer',
'Study Place',
'Meeting Room',
'Cafeteria',
'Restroom',
'Stair',
'Lift',
'Exit'
],

bottomCount1: [
'Printer',
'Computer',
'Study Place',
'Meeting Room',
'Cafeteria',
'Restroom'
]
};
},
methods: {

backhome () {
this.$router.push({path: '/'});
},
searchBack () {
if (this.valueSearch) {
this.$router.push({name: 'Vmuniversity', params: {selectFloor: true, page: 3}});
}
},
getTower (i) {
this.valueSearch = i;
},

...mapActions(['loadPageConfig'])
},

components: {
XHeader,
Tab,
TabItem,
Flexbox,
FlexboxItem,
XImg,
Swiper,
SwiperItem,
Scroller,
LoadMore
},
store
};



