import Vue from 'vue';
import Router from 'vue-router';
import Home from '@/page/VmHome';
import Index from '@/page/VmIndex';
import Tower from '@/page/VmTower';
import Maps from '@/page/VmMap';
import Vmuniversity from '@/page/Vmuniversity';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/index',
      name: 'Index',
      component: Index
    },
    {
      path: '/tower',
      name: 'Tower',
      component: Tower
    },
    {
      path: '/map',
      name: 'Map',
      component: Maps
    },
    {
      path: '/university',
      name: 'Vmuniversity',
      component: Vmuniversity
    }
  ]
});



