import axios from 'axios';

const http = axios.create({
  timeout: 5000
});

http.interceptors.response.use(function (response) {
  // 
  return response.data;
}, (error) => {
  // 
  console.log(error.response.status);
  return Promise.reject(error);
});

export default http;



